.footer ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: white;
}

.navbar .navbar__link {
  display: inline-block;
  color: #282c34;
  font-size: 1rem;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.navbar .navbar__link--active {
  border-bottom: 2px solid #61dafb;
}

.fl-l {
  float: left;
}

.fl-r {
  float: right;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.link {
  color: #61dafb;
}
